
  .navbar_ {
    /*overflow: hidden;*/
    background-color: #333;
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-end;
  }
  
  .navbar_ a {
    float: left;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  
  }
  
  .dropdown {
    float: left;
    /*overflow: hidden;*/
  }
  
  .dropdown .dropbtn {
    font-size: 16px;  
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
    position: sticky;
    right: 5px;
  }
  
  .navbar_ a:hover, .dropdown:hover .dropbtn {
    background-color: white;
    color: black;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    right: 10px;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1000;
  }
  
  .dropdown-content a {
    float: none;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
  }
  
  .dropdown-content a:hover {
    background-color: #ddd;
  }
  
  .dropdown:hover .dropdown-content {
  /*.dropdown .dropdown-content {*/
    display: block;
    z-index: 1000;
  }
  .title-container{
    padding: 20px 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
