.sponsor-img{
    background-color: white;
    width:7em;
    height:7em;
}
.sponsor-img-container{
    margin:1em;
}
.sponsors-container{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
.divider{
    height: 2px;
    width: 95%;
    background-color: rgb(88, 88, 88);
    margin: auto;
}

input{
    border-width: 0px;
    border-bottom: solid 1px blue;
    padding: 7px;
    margin:10px 0px;
    width: 100%;
    font-size: medium;
}
textarea{
    width: 100%;
    resize: vertical;
    font-size: medium;
    padding: 7px;
}
.row{
    width: 100%;
    display: flex;
    flex-direction: row;
    margin: 0px;
    padding: 0px;
    justify-content: space-between;
}
.contact{
    display: flex;
    max-width: 700px;
    align-items: center;
    flex-direction: column;
    align-self: center;
    margin-bottom: 50px;
    margin:7px;
}
.submit{
    background-color: #1593c5;
    border:0px;
    color: white;
    width: max-content;
    padding: 13px;
    font-size: medium;
}
.submit:hover{
    cursor: pointer;
    user-select: none;
    background-color: #165f7c;
}