.projects{
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    /* background-color: red; */
}


h1{
    text-align: left;
    padding:1rem;
}
.container{
    min-width: 500px;
    max-width: 700px;
    /* border:solid 2px black; */
    padding: 10px;
    margin: 10px;
}
.principal{
    display: flex;
    flex-direction: row;
    /*flex-wrap: wrap;*/
}
.image{
    display: flex;
    justify-content: center;
    align-items: center;
}
.details{
    padding: 10px;
}
.nom{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
}
.competences{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.competence{
    background-color: #E5E8EA;
    border-radius: 5px;
    padding: 10px;
    margin: 5px;
    border:solid 2px black;
}
.competence:hover{
    background-color: #a1b9c9;
    user-select: none;
    cursor: pointer;
}
.title{
    margin: 10px;
    font-weight: bold;
    font-size: 15px;
    color: black;
    text-align: left;
}
.detail{
    margin: 10px;
}
.more{
    color: #3F7FE0;
}
.more:hover{
    cursor: pointer;
    user-select: none;
}